body {
    font-family: 'Segoe UI', Arial, sans-serif;
    font-size: 2vw;
    text-align: center;
    background: linear-gradient(135deg, #0033ff 0%, #ff1100 100%);
    color: #fff;
    min-height: 100vh;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.App {
    background: rgba(255,255,255,0.08);
    border-radius: 20px;
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    padding: 1vw 2vw;
    margin: 2vw;
    max-width: 5000px;
    width: 90vw;
}

.Countdown {
    background: rgba(255,255,255,0.08);
    border-radius: 20px;
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    padding: 1.5vw 2vw;
    margin: 2vw auto;
    width: 25vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

h1 {
    font-size: 3vw;
    margin-bottom: 1vw;
    font-weight: 700;
    letter-spacing: 1px;
    text-shadow: 0 2px 8px rgba(0,0,0,0.15);
}

button {
    font-size: 1.5vw;
    padding: 0.8vw 2vw;
    border: none;
    border-radius: 8px;
    background: linear-gradient(90deg, #fff 0%, #f9d423 100%);
    color: #ff4e50;
    font-weight: 600;
    box-shadow: 0 2px 8px rgba(0,0,0,0.10);
    cursor: pointer;
    transition: background 0.3s, color 0.3s, box-shadow 0.3s;
}

button:hover {
    background: linear-gradient(90deg, #ff4e50 0%, #f9d423 100%);
    color: #fff;
    box-shadow: 0 4px 16px rgba(0,0,0,0.18);
}

.countdown-label {
    font-size: 2vw;
    margin-bottom: 0.5vw;
    color: #fff;
    opacity: 0.95;
    font-weight: 700;
    letter-spacing: 1px;
    text-shadow: 0 2px 8px rgba(0,0,0,0.15);
}

#CountDown, .countdown-timer {
    font-size: 2vw;
    margin: 2vw 0;
    font-weight: 600;
    letter-spacing: 2px;
    color: #fff;
    text-shadow: 0 2px 8px rgba(0,0,0,0.15);
}

.countdown-timer {
    font-size: 2vw;
    margin: 2vw 0;
    font-weight: 600;
    letter-spacing: 2px;
    color: #ff4e50;
    background: linear-gradient(90deg, #13f7ff 0%, #00ff08 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-shadow: 0 2px 8px rgba(255,76,80,0.25), 0 0px 24px #f9d423;
    transition: color 0.3s, text-shadow 0.3s;
}

.love-message {
    margin-bottom: 1vw;
    font-size: 1.3vw;
    color: #ffe4e1;
    font-weight: 500;
    text-shadow: 0 2px 8px rgba(255,192,203,0.18);
    text-align: center;
}

@media (max-width: 600px) {
    .App {
        max-width: 100vw;
        width: 90vw;
        margin: 0;
        padding: 0;
        border-radius: 20px;
        box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
        background: rgba(255,255,255,0.08);
        padding: 1vw 2vw;
    }
    .Countdown {
        width: 80vw;
        max-width: 80vw;
        margin: 4vw auto;
        padding: 4vw 2vw;
        border-radius: 16px;
        box-shadow: 0 4px 16px 0 rgba(31, 38, 135, 0.18);
    }
    .countdown-label {
        font-size: 5vw;
    }
    .countdown-timer {
        font-size: 6vw;
    }
    .Countdown {
        width: 90vw;
        padding: 4vw 2vw;
    }
    .love-message {
        font-size: 4vw;
        margin-bottom: 3vw;
    }
    h1 {
        font-size: 8vw;
        margin-bottom: 3vw;
    }
}